import Stack from './stack';

const opList = ['+', '-', '*', '/'];
const leftScope = ['(', '[', '{'];
const rightScope = [')', ']', '}'];
const opPriority = {
  '+': 1,
  '-': 1,
  '*': 2,
  '/': 2,
}

export default function RPNTransform(str){
  const arr = str.split();
  let opStr = '';
  let stack = new Stack();
  arr.reduce((acc, cur) => {
    if([...opList, ...leftScope, ...rightScope].indexOf(cur) !== -1){
      if(acc !== ''){
        opStr += `${cur} `;
      }
      if(stack.isEmpty()){
        stack.push(acc);
      }
      else{
        const indexRight = rightScope.indexOf(cur);
        if(indexRight !== -1){
          let topEle = stack.pop();
          while(topEle !== leftScope[indexRight]){
            opStr += `${topEle} `;
          }
        }
        else if(leftScope.indexOf(cur) !== -1 || opPriority[stack.getTopEle()] <= opPriority[cur]){
          stack.push(acc);
        }
        else{
          let topEle = stack.pop();
          while(leftScope.indexOf(topEle) !== -1){
            opStr += `${topEle} `
          }
          stack.push(acc)
        }
      }
    }
    else{
      acc += cur;
    }
    console.log(acc, '---acc');
    console.log(opStr, '--- RPNStr');
  }, '')
}